<template>
  <div id="app">
    <div class="item-list">
      <p style="text-align: center;" v-if="!lives.length">
        
      </p>
      <div class="live-item" v-for="(liveItem, index) in lives" :key="index">
        <a
          href="javascript:;"
          :title="liveItem.videoTitle"
          @click="play(liveItem)"
        >
          <img class="v-thumbnail" :src="getThumbnailUrl(liveItem.thumbnail)" />
          <div class="card-mark">
            <p class="title">
              {{ liveItem.videoTitle }}
            </p>
            <p class="author">{{ liveItem.accountId }}</p>
          </div>
        </a>
      </div>
    </div>
    <el-dialog
      :visible.sync="playerShow"
      :title="currentLiveItem.videoTitle"
      width="890px"
      :close-on-click-modal="false"
      @opened="startPlay"
      @close="stopPlay"
    >
      <video id="videoElement" width="100%" height="100%" controls />
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      lives: [],
      playerShow: false,
      currentLiveItem: {}
    };
  },
  methods: {
    play(liveItem) {
      this.currentLiveItem = liveItem;
      this.playerShow = true;
      if (liveItem.accountSite == "17live") {
        this.currentLiveItem.videoUrl =
          "http://wansu-china-pull-rtmp-17.winwinsun.com/vod/" +
          liveItem.uid +
          ".flv";
      }
    },
    startPlay() {
      var videoElement = document.getElementById("videoElement");
      window.flvPlayer = flvjs.createPlayer({
        type: "flv",
        isLive: true,
        url: this.currentLiveItem.videoUrl
      });
      flvPlayer.attachMediaElement(videoElement);
      flvPlayer.load();
      flvPlayer.play();
    },
    stopPlay() {
      this.playerShow = false;
      window.flvPlayer.destroy();
    },
    getThumbnailUrl(thumbnail) {
      return window.aliceHost + thumbnail;
    }
  },
  created() {
    this.$http.get(window.aliceHost + "/api/magictea/liveNow.json").then(
      function(response) {
        this.lives = response.data;
      },
      function(response) {
        this.$message.error("" + response.status);
      }
    );
  }
};
</script>

<style>
#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "", Arial, sans-serif;
  height: 100%;
  font-size: 14px;
}

html,
body {
  margin: 0;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}

hr {
  border: none;
  border-top: 1px #ccc solid;
}

a {
  color: #409eff;
  text-decoration: none;
}

article,
aside,
blockquote,
body,
button,
code,
dd,
details,
div,
dl,
dt,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
hr,
input,
legend,
li,
menu,
nav,
ol,
p,
pre,
section,
td,
textarea,
th,
ul {
  margin: 0;
  padding: 0;
}

.od-config .el-message-box__input {
  padding-top: 0px;
}

.el-dialog__body {
  padding: 20px 20px;
}

.item-list {
  padding: 100px;
  margin: 0 auto;
}

.live-item {
  width: 320px;
  height: 190px;
  position: relative;
  overflow: hidden;
  border-radius: 5px;
  background: #fff;
  cursor: pointer;
  margin: 10px;
}

.live-item .card-mark {
  position: absolute;
  left: 0;
  bottom: 0px;
  width: 310px;
  height: 100%;
  font-size: 12px;
  height: 40px;
  line-height: 20px;
  padding: 10px 5px;
  overflow: hidden;
  background: #000;
  background: linear-gradient(
    transparent,
    rgba(0, 0, 0, 0.1) 20%,
    rgba(0, 0, 0, 0.2) 35%,
    rgba(0, 0, 0, 0.6) 65%,
    rgba(0, 0, 0, 0.9)
  );
}

.live-item .v-thumbnail {
  width: 320px;
  height: 190px;
  border: none;
}

.live-item .card-mark .title {
  color: #fff;
  height: 20px;
  overflow: hidden;
  margin-bottom: 5px;
  word-break: break-all;
  word-wrap: break-word;
}

.live-item .card-mark .author,
.live-item .card-mark .play {
  color: #99a2aa;
  height: 20px;
  line-height: 20px;
  overflow: hidden;
  transition: all 0.6s;
}
</style>



